<script>
export default {
  name: "FirstQuestion",
  data() {
    return {
      valName: null,
      valAnswer: null,
      valEmail: null,
      detailAboutAnswers: [],
      editingItem: null,
      editItemAnswer: null
    };
  },
  computed: {
    dateToday() {
      var d = new Date(),
        y = d.getFullYear(),
        m = d.getMonth(),
        dy = d.getDate();
      return dy + "/" + m + "/" + y;
    }
  },
  mounted() {
    if (localStorage.getItem("store")) {
      this.detailAboutAnswers = JSON.parse(localStorage.getItem("store"));
    }
  },
  methods: {
    doneEdit() {
      this.editItemAnswer = null;
    },
    editThis() {
      this.editingItem = null;
    },
    editAnswer(item) {
      if (item.name == "" && item.answer == "") {
        return false;
      } else {
        this.editingItem = item;
        this.editItemAnswer = item;
      }
    },
    deleteAnswer(index) {
      this.detailAboutAnswers.splice(index, 1);
    },
    addAnswer() {
      const detail = {
        name: this.valName,
        answer: this.valAnswer
      };
      if (!(this.valAnswer && this.valName)) {
        return false;
      } else {
        this.detailAboutAnswers.unshift(detail);
      }
      (this.valAnswer = null), (this.valName = null), (this.valEmail = null);
    }
  },
  watch: {
    detailAboutAnswers: {
      handler() {
        localStorage.setItem("store", JSON.stringify(this.detailAboutAnswers));
      },
      deep: true
    }
  }
};
</script>

