<template>
  <div class="header">
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
      <div class="container">
        <!----Start LOGO---->
        <router-link class="navbar-brand" to="./">
          <img src="@/assets/logo.png" />
        </router-link>
        <!----End LOGO---->

        <!--- Start Menu---->
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <!--- End Menu---->

        <!--Start Lists-->
        <div class="collapse navbar-collapse" id="navbarNav">
          <!-- Start left part -->
          <ul class="navbar-nav mr-auto">
            <li class="nav-item">
              <router-link active-class="active" class="nav-link" to="/" exact
                >{{ $t("Home") }}
                <span class="sr-only">(current)</span></router-link
              >
            </li>
            <li class="nav-item">
              <router-link
                active-class="active"
                class="nav-link"
                to="/contact"
                >{{ $t("Contact") }}</router-link
              >
            </li>
            <li class="nav-item">
              <router-link active-class="active" class="nav-link" to="/about">{{
                $t("About US")
              }}</router-link>
            </li>
            <li class="nav-item">
              <select
                name=""
                id=""
                class="custom-select"
                v-model="lang"
                @change="handleChange($event)"
              >
                <option value="en"
                  >English <i class="fas fa-angle-down"></i
                ></option>
                <option value="ar">عربي</option>
              </select>
            </li>
          </ul>
          <!-- end left part -->

          <!-- Start right part -->
          <div class="left my-2 my-lg-0">
            <a href="/register"
              ><button class="btn btn-outline-success my-2 my-sm-0">
                {{ $t("Sign In") }}
              </button></a
            >
            <a href="/register"
              ><button class="btn btn-outline-success my-2 my-sm-0">
                {{ $t("Sign Up") }}
              </button></a
            >
          </div>
          <!-- end right part -->
        </div>
        <!--Start Lists-->
      </div>
    </nav>
  </div>
</template>

<script>
export default {
  name: "navHeader",
  data() {
    return {
      lang: localStorage.getItem("lang") || "en"
    };
  },
  methods: {
    handleChange(event) {
      localStorage.setItem("lang", event.target.value);
      window.location.reload();
    }
  }
};
</script>

<style lang="scss" scoped>
.navbar {
  padding: 0 !important;
  .container {
    /*****Start LOGO*****/
    .navbar-brand {
      border-right: 1px solid #4d4d4d;
      padding: 13px 23px;
    }
    /*****End LOGO*****/

    /****Start Lists****/
    .collapse {
      /*** Start left part *****/
      .navbar-nav {
        padding-left: 20px;
        .nav-item {
          position: relative;
          a {
            &.active {
              background-color: #000;
            }
          }
          .custom-select {
            margin-left: 14px;
            color: rgba(255, 255, 255, 0.5) !important;
            background: #343a40 !important;
            border: 1px solid #343a40 !important;
            cursor: pointer;
            appearance: auto !important;
            &:focus {
              outline: 0;
              box-shadow: none !important;
            }
          }
        }
      }
      /*** End left part *****/

      /*** Start Right part *****/
      .left {
        height: 71px;
        position: relative;
        border-left: 1px solid #4d4d4d;
        padding: 0;
        margin: 0;
        float: right;
        // width: 24%;
        width: 42%;
        @media (max-width: 992px) {
          width: 80% !important;
        }
        padding-left: 19px;
        top: 0px;
        line-height: 71px;
        a {
          button {
            font-family: sans-serif;
            font-weight: 600;
            letter-spacing: 1.1px;
            outline: 0;
            &:focus {
              box-shadow: none !important;
              outline: 0;
            }
          }
          &:first-of-type {
            button {
              margin: 5px;
              background: #2d6ff7;
              color: #fff;
              border: 1px solid #2d6ff7;
            }
          }
          &:last-of-type {
            button {
              background: #fff;
              border: 1px solid #fff;
              color: #333;
            }
          }
        }
      }
      /*** End Right part *****/
    }
    /****End Lists****/
  }
}
</style>
